// import uniIcon from "../../../components/uni-icon/uni-icon.vue"
import {
	uniIcon
} from '@dcloudio/uni-ui/lib/uni-icon/uni-icon.vue'
export default {
	data() {
		return {
			title1: '全部',
			title2: '全部',
			title3: '全部',
			active1: 1,
			active2: 1,
			active3: 1,
			show: false,
			show1: false,
			show2: false,
			show3: false,
			checkAll: false,
			list: ['a', 'b', 'c'],
			result: ['a', 'b'],
			thesaurusLists: [],
			chapterLists: []

		};
	},
	components: {
		uniIcon
	},
	watch: {
		title1() {
			console.log(this.title1)
			this.chapterList(this.title1)
			this.title2 = "全部"
			this.title3 = "全部"
		},
		title2() {
			
		}
	},
	methods: {
		actives1() {
			if (this.show == true && (this.show2 == true || this.show3 == true)) {
				this.show = this.show
				console.log(1)
			} else if (this.show == true && this.show1 == false) {
				this.show = false
				console.log(2)
			} else {
				this.show = !this.show
				console.log(3)
			}
			console.log(this.show)
			this.active1 = !this.active1
			this.show1 = !this.show1
			this.show2 = false
			this.show3 = false
			this.active2 = false
			this.active3 = false
			console.log(this.active1, 'active1')
		},
		actives2() {
			if (this.show == true && (this.show1 == true || this.show3 == true)) {
				this.show = this.show
			} else {
				this.show = !this.show
			}

			this.active2 = !this.active2
			this.show2 = !this.show2
			this.show1 = false
			this.show3 = false
			this.active1 = false
			this.active3 = false
			console.log(this.active2, 'active2')
		},
		actives3() {
			if (this.show == true && (this.show1 == true || this.show2 == true)) {
				this.show = this.show
			} else {
				this.show = !this.show
			}

			this.show1 = false
			this.show2 = false
			this.active2 = false
			this.active1 = false
			this.active3 = !this.active3
			this.show3 = !this.show3
			console.log(this.active3)
		},
		optionsActive1(val) {
			this.title1 = val
			console.log(this.title1, this.thesaurusId1)
			this.show = !this.show
			this.show1 = false
			this.active1 = !this.active1
		},
		optionsActive2(val) {
			this.title2 = val
			console.log(val)
			this.show = !this.show
			this.show2 = false
			this.active2 = !this.active2
		},
		optionsActive3(val) {
			this.title3 = val
			console.log(val)
			this.show = !this.show
			this.show3 = false
			this.active3 = !this.active3
		},
		onChange(event) {
			console.log(event.detail)
			this.checkAll = event.detail
		},
		onChanges(event) {
			this.result = event.detail
			console.log(this.result)
		},
		// 获取课程下拉列表
		thesaurusList() {
			this.$minApi.thesaurusList({}).then(data => {
				console.log(data)
				this.thesaurusLists = data.data
			})
		},
		// 获取章节下拉列表
		chapterList(thesauruName) {
			this.$minApi.chapterList({
				thesauruName: thesauruName
			}).then(data => {
				console.log(data)
				this.chapterLists = data.data
			})
		},
	},
	created() {
		this.thesaurusList()
	}
}
